@font-face {
  font-family: "Inter-ExtraBold";
  src: url("../fonts/Inter/Inter-ExtraBold.eot");
  src: url("../fonts/Inter/Inter-ExtraBold.eot?#iefix") format("embedded-opentype"), url("../fonts/Inter/Inter-ExtraBold.woff2") format("woff2"), url("../fonts/Inter/Inter-ExtraBold.woff") format("woff"), url("../fonts/Inter/Inter-ExtraBold.ttf") format("truetype"), url("../fonts/Inter/Inter-ExtraBold.svg#Inter-ExtraBold") format("svg");
  font-weight: 800;
  font-style: normal;
}
@font-face {
  font-family: "Inter-Bold";
  src: url("../fonts/Inter/Inter-Bold.eot");
  src: url("../fonts/Inter/Inter-Bold.eot?#iefix") format("embedded-opentype"), url("../fonts/Inter/Inter-Bold.woff2") format("woff2"), url("../fonts/Inter/Inter-Bold.woff") format("woff"), url("../fonts/Inter/Inter-Bold.ttf") format("truetype"), url("../fonts/Inter/Inter-Bold.svg#Inter-Bold") format("svg");
  font-weight: 700;
  font-style: normal;
}
@font-face {
  font-family: "Inter-Regular";
  src: url("../fonts/Inter/Inter-Regular.eot");
  src: url("../fonts/Inter/Inter-Regular.eot?#iefix") format("embedded-opentype"), url("../fonts/Inter/Inter-Regular.woff2") format("woff2"), url("../fonts/Inter/Inter-Regular.woff") format("woff"), url("../fonts/Inter/Inter-Regular.ttf") format("truetype"), url("../fonts/Inter/Inter-Regular.svg#Inter-Regular") format("svg");
  font-weight: 400;
  font-style: normal;
}
a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

p {
  line-height: 25px;
}

body {
  background-color: hsl(36, 100%, 99%);
  color: hsl(236, 13%, 42%);
  font-family: "Inter-Regular";
  font-size: 15px;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

header {
  position: relative;
  padding: 85px 0 55px;
}
header .top-navigation-main {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
header .top-navigation-main .mobile-menu {
  display: none;
}
header .top-navigation-main .main-menu {
  position: relative;
}
header .top-navigation-main .main-menu .mobile-menu-close {
  display: none;
}
header .top-navigation-main .main-menu ul {
  margin: 0;
  padding: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  list-style: none;
}
header .top-navigation-main .main-menu ul li {
  margin-left: 35px;
}
header .top-navigation-main .main-menu ul li a {
  color: hsl(236, 13%, 42%);
  font-size: 16px;
}
header .top-navigation-main .main-menu ul li a:hover {
  color: hsl(5, 85%, 63%);
}

.articles {
  position: relative;
}
.articles .featured {
  position: relative;
  overflow: hidden;
}
.articles .featured figure {
  margin: 0;
  transition: 0.4s ease;
}
.articles .featured figure:hover {
  transform: scale(1.08);
}
.articles .featured figure img {
  width: 100%;
}
.articles .featured .content {
  position: relative;
  margin-top: 25px;
}
.articles .featured .content h2 {
  margin: 0px;
  color: hsl(240, 100%, 5%);
  font-size: 50px;
  font-family: "Inter-ExtraBold";
}
.articles .featured .content p {
  margin: 0;
  margin-bottom: 15px;
}
.articles .featured .content .readmore {
  position: relative;
  vertical-align: bottom;
}
.articles .featured .content .readmore a {
  background-color: hsl(5, 85%, 63%);
  color: hsl(36, 100%, 99%);
  display: inline-block;
  padding: 13px 25px;
  text-transform: uppercase;
  letter-spacing: 5px;
  transition: 0.3s;
}
.articles .featured .content .readmore a:hover {
  background-color: hsl(240, 100%, 5%);
  color: hsl(233, 8%, 79%);
}
.articles .sidebar {
  position: relative;
  background-color: hsl(240, 100%, 5%);
  height: 100%;
  width: 100%;
  display: flex;
}
.articles .sidebar .news {
  display: inline-block;
  padding: 15px;
}
.articles .sidebar .news h3 {
  color: hsl(35, 77%, 62%);
  font-family: "Inter-Bold";
  font-size: 30px;
  margin: 15px 0px 30px;
}
.articles .sidebar .news article {
  position: relative;
}
.articles .sidebar .news article h4 {
  color: hsl(36, 100%, 99%);
  font-family: "Inter-Bold";
  font-size: 20px;
  margin: 0;
  margin-bottom: 10px;
  transition: 0.3s;
}
.articles .sidebar .news article h4:hover {
  color: hsl(35, 77%, 62%);
}
.articles .sidebar .news article p {
  margin: 0;
  color: hsl(233, 8%, 79%);
}
.articles .sidebar .news hr {
  border-bottom: 1px solid hsl(233, 8%, 79%);
  margin: 25px 0px;
}

.top-articles {
  margin-top: 72px;
  margin-bottom: 50px;
}
.top-articles article {
  position: relative;
}
.top-articles article figure {
  margin: 0;
  float: left;
  margin-right: 25px;
}
.top-articles article figure img {
  width: 100px;
}
.top-articles article .content {
  position: relative;
}
.top-articles article .content .number span {
  font-family: "Inter-Bold";
  color: hsl(233, 8%, 79%);
  font-size: 25px;
}
.top-articles article .content h4 {
  margin: 15px 0px 0px;
  color: hsl(240, 100%, 5%);
  font-family: "Inter-Bold";
  font-size: 20px;
  transition: 0.3s;
}
.top-articles article .content h4:hover {
  color: hsl(5, 85%, 63%);
}
.top-articles article .content p {
  margin: 10px 0px 0px;
}

.attribution {
  font-size: 11px;
  text-align: center;
  margin-bottom: 15px;
}

.attribution a {
  color: hsl(228, 45%, 44%);
}

@media only screen and (max-width: 767px) {
  header {
    padding: 40px 0px;
  }
  header .top-navigation-main .main-menu {
    display: none;
  }
  header .top-navigation-main .main-menu .mobile-menu-close {
    display: block;
  }
  header .top-navigation-main .mobile-menu {
    display: block;
  }
  header .top-navigation-main .mobile-menu .bar1,
  header .top-navigation-main .mobile-menu .bar2,
  header .top-navigation-main .mobile-menu .bar3 {
    width: 35px;
    height: 2px;
    background-color: #333;
    margin: 6px 0;
    transition: 0.4s;
  }
  header .top-navigation-main .change .bar1 {
    transform: translate(0, 11px) rotate(-45deg);
  }
  header .top-navigation-main .change .bar2 {
    opacity: 0;
  }
  header .top-navigation-main .change .bar3 {
    transform: translate(0, -5px) rotate(45deg);
  }
}